.home-page {
  width: 100%;
  height: 100vh; }

.home-page__content {
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  height: 100%;
  width: 100%; }

.home-page__text {
  background: white;
  color: black;
  font-weight: lighter;
  position: relative;
  font-size: 3em; }
  .home-page__text::before {
    background: -webkit-gradient(linear, left top, right top, from(#0cdaec), to(#b1f54d));
    background: -webkit-linear-gradient(left, #0cdaec, #b1f54d);
    background: -o-linear-gradient(left, #0cdaec, #b1f54d);
    background: linear-gradient(to right, #0cdaec, #b1f54d);
    content: '';
    bottom: 0;
    display: block;
    position: absolute;
    left: 0;
    mix-blend-mode: screen;
    right: 0;
    top: 0; }

@media screen and (min-height: 765px) {
  .home-page__text {
    font-size: 4em; } }



