.message-impact {
  padding-top: 2em;
  position: relative; }

.message-impact__image {
  -webkit-animation: fade-in 1s ease-in-out;
          animation: fade-in 1s ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  border: 2px solid #00afbf;
  border-radius: 50%;
  -webkit-box-shadow: 0px 29px 36px -20px #333;
          box-shadow: 0px 29px 36px -20px #333;
  left: 0;
  margin-left: 50%;
  position: absolute;
  top: 3em;
  will-change: transform, opacity;
  z-index: 1; }

.central-pin,
.small-pin,
.big-pin {
  -webkit-animation: fade-in 1s ease-in-out;
          animation: fade-in 1s ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  opacity: 0;
  position: absolute; }

.big-pin {
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
  right: 0;
  top: -1em;
  -webkit-transform: translateX(-50%) translateX(2.5em);
      -ms-transform: translateX(-50%) translateX(2.5em);
          transform: translateX(-50%) translateX(2.5em);
  width: 100%; }

.central-pin {
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
  position: absolute;
  right: 0;
  top: -1em;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 50%; }

.small-pin {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  right: 0;
  top: 1em;
  -webkit-transform: translateX(-50%) translateX(2.5em);
      -ms-transform: translateX(-50%) translateX(2.5em);
          transform: translateX(-50%) translateX(2.5em);
  width: 100%; }

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
    -webkit-transform: translateX(-50%) translateY(-1em);
            transform: translateX(-50%) translateY(-1em); }
  to {
    opacity: 1;
    -webkit-transform: translateX(-50%) translateY(0);
            transform: translateX(-50%) translateY(0); } }

@keyframes fade-in {
  from {
    opacity: 0;
    -webkit-transform: translateX(-50%) translateY(-1em);
            transform: translateX(-50%) translateY(-1em); }
  to {
    opacity: 1;
    -webkit-transform: translateX(-50%) translateY(0);
            transform: translateX(-50%) translateY(0); } }



