.message-avatar {
  padding-left: 0;
  position: relative; }

.message-avatar__badge {
  background-color: #00afbf;
  border-radius: 50%;
  height: 9px;
  left: -1em;
  position: absolute;
  top: .75em;
  width: 9px; }

.message-avatar__campaign {
  border: 2px solid white;
  border-radius: 50%;
  top: 25px;
  position: absolute;
  right: .5em; }

.message-avatar__image {
  border-radius: 10%;
  margin-right: 1em; }

@media screen and (min-width: 765px) {
  .message-avatar {
    padding-left: 2em; }
  .message-avatar__badge {
    left: .5em; } }



