.search {
  position: relative; }

.search__icon {
  fill: #a9a9a9;
  height: 1.5em;
  left: 1em;
  position: absolute;
  top: .6em; }

.search__input {
  background-color: white;
  border: none;
  border-bottom: 1px solid #d6d6d6;
  border-top: 1px solid #d6d6d6;
  -webkit-box-shadow: 0px 1px 1px 0px #d6d6d6;
          box-shadow: 0px 1px 1px 0px #d6d6d6;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #a9a9a9;
  font-size: 1.2em;
  margin: 0;
  outline: none;
  padding: .5em 1em .5em 3em;
  width: 100%;
  z-index: 9; }

@media screen and (min-width: 765px) {
  .search__input {
    border-bottom: 1px solid #d6d6d6;
    border-top: none;
    -webkit-box-shadow: 0px 1px 2px 0px #f2f2f2;
            box-shadow: 0px 1px 2px 0px #f2f2f2; } }



