.navbar-link {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer;
  display: block;
  height: 90px;
  margin: .25em 0;
  padding: .5em 10% 1em 10%;
  text-decoration: none;
  width: 100%; }

.navbar-link:hover {
  background: #272b2b;
  margin: .25em 10%;
  padding: .5em 0 1em 0;
  width: 80%; }

.navbar-link.active,
.navbar-link.active:hover {
  background: #00afbf;
  color: white;
  margin: .25em 0;
  padding: .5em 10% 1em 10%;
  width: 100%; }

.navbar-link__label {
  color: #d8d8d8;
  display: block;
  overflow: hidden;
  text-align: center;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%; }

.navbar-link:hover .navbar-link__label,
.navbar-link.active .navbar-link__label {
  color: #ffffff; }

.navbar-link:hover svg g,
.navbar-link.active svg g {
  fill: #ffffff; }

.navbar-link.active img {
  border: 2px solid #ffffff; }



